//basic workflow imports:
import { useState, useEffect } from "react";
//styling imports:
//mui ->
import {
  Button,
  TextField,
  FormGroup,
  AppBar,
  Typography,
  FormControl,
  Collapse,
  Container,
  CssBaseline,
  Stack,
  Switch,
  FormControlLabel,
  Toolbar,
  CardHeader,
  Avatar,
} from "@mui/material";
import { ThemeProvider, createTheme } from "@mui/material/styles";
import { grey, red, green, blue, purple, common } from "@mui/material/colors";
import { Delete, DriveFileRenameOutline, Send } from "@mui/icons-material";
//website logo import:
import { ReactComponent as Logo } from "../styles/FakeBook.svg";
// local components imports:
import RegisterForm from "./RegisterForm";
import LoginForm from "./LoginForm";

// receiving props:
interface NavBarProps {
  setTheme: Function;
  theme: any;
  lightTheme: any;
  darkTheme: any;
  handleRegistration: Function;
  handleLogin: Function;
}

//sending props:
interface RegisterFormProps {
  theme: any;
  lightTheme: any;
  darkTheme: any;
  handleRegistration: Function;
}
interface LoginFormProps {
  theme: any;
  lightTheme: any;
  darkTheme: any;
  handleLogin: Function;
}

function NavBar(props: NavBarProps) {
  const [registered, setRegistered] = useState(true);

  const {
    setTheme,
    theme,
    lightTheme,
    darkTheme,
    handleRegistration,
    handleLogin,
  } = props;
  if (theme) {
    var { primary, secondary, background } = darkTheme.palette;
  } else {
    var { primary, secondary, background } = lightTheme.palette;
  }
  return (
    <AppBar position="static" color={theme ? "secondary" : "primary"}>
      <Toolbar>
        <Logo fill={background.default} stroke={background.default} />
        <Switch
          color="secondary"
          onChange={() => {
            setTheme(!theme);
          }}
        />
        <RegisterForm
          theme={theme}
          lightTheme={lightTheme}
          darkTheme={darkTheme}
          handleRegistration={handleRegistration}
        />
        <LoginForm
          theme={theme}
          lightTheme={lightTheme}
          darkTheme={darkTheme}
          handleLogin={handleLogin}
        />
      </Toolbar>
    </AppBar>
  );
}

export default NavBar;
