//basic workflow imports:
import { useState, useEffect } from "react";
import axios from "axios";
//styling imports:
//mui ->
import {
  Avatar,
  Button,
  TextField,
  FormGroup,
  AppBar,
  Card,
  CardActions,
  CardContent,
  CardHeader,
  CardMedia,
  Collapse,
  Container,
  CssBaseline,
  FormControl,
  FormControlLabel,
  Grid,
  InputLabel,
  Stack,
  Switch,
  Select,
  Toolbar,
  Typography,
  MenuItem,
} from "@mui/material";
import { ThemeProvider, createTheme } from "@mui/material/styles";
import { grey, red, green, blue, purple, common } from "@mui/material/colors";
import { Delete, DriveFileRenameOutline, Send } from "@mui/icons-material";

// Create props:
interface RegisterFormProps {
  handleRegistration: Function;
  theme: any;
  lightTheme: any;
  darkTheme: any;
}

function RegisterForm(props: RegisterFormProps) {
  const { handleRegistration, theme, lightTheme, darkTheme } = props;
  const [gender, setGender] = useState("Gender");
  return (
    <form onSubmit={(ev) => handleRegistration(ev)}>
      <FormGroup row>
        <FormControlLabel
          control={
            <FormGroup row>
              <TextField
                name="firstName"
                id="firstName"
                placeholder="First Name"
              />
              <TextField
                name="lastName"
                id="lastName"
                placeholder="Last Name"
              />
              <TextField
                type="date"
                name="birthDate"
                id="birthDate"
                placeholder="whats your birth date?"
              />
              <Select
                renderValue={(selected) => {
                  if (selected.length === 0) {
                    return <em>Gender</em>
                  }

                  return selected;
                }}
                name="gender"
                autoWidth
                id="gender"
                label="gender"
                value={gender}
                onChange={(ev) => {
                  setGender(ev.target.value);
                }}
              >
                 <MenuItem disabled value="Gender">
            Gender
          </MenuItem>
                <MenuItem value="male">Male</MenuItem>
                <MenuItem value="female">Female</MenuItem>
              </Select>
            </FormGroup>
          }
          label="Personal information"
          labelPlacement="top"
        />
        <FormControlLabel
          control={
            <FormGroup row>
              <TextField name="username" id="username" placeholder="Username" />
              <TextField
                type="email"
                name="email"
                id="email"
                placeholder="Email"
              />
              <TextField
                type="password"
                name="password"
                id="password"
                placeholder="Password"
              />
              <TextField
                type="password"
                name="confirmPassword"
                id="confirmPassword"
                placeholder="Password"
              />
              <TextField
              name="role"
              id="role"
              placeholder="Role"/>
            </FormGroup>
          }
          label="Login information"
          labelPlacement="top"
        />
        <Button
          type="submit"
          color={theme ? "primary" : "secondary"}
          variant="outlined"
        >
          Sign Up!
        </Button>
      </FormGroup>
    </form>
  );
}

export default RegisterForm;
