//receiving props:
interface PostsProps {
  setPostsList: Function;
  post: PostInfoProps;
  theme: any;
  lightTheme: any;
  darkTheme: any;
}
interface NavBarProps {
  setTheme: Function;
  theme: any;
  lightTheme: any;
  darkTheme: any;
  handleRegistration: Function;
  handleLogin: Function;
}
interface FeedProps {
  postsList: Array<PostInfoProps>;
  theme: any;
  setPostsList: Function;
  lightTheme: any;
  darkTheme: any;
}
interface NewPostFormProps {
  setPostsList: Function;
}
//sending props:
interface PostInfoProps {
  completed: boolean;
  id: number;
  title: string;
  userId: string;
}
const darkTheme = createTheme({
  palette: {
    primary: {
      light: blue[200],
      main: "#28376f",
      dark: blue[900],
      contrastText: common.white,
    },
    secondary: {
      main: blue[500],
    },
    background: {
      default: blue[900],
    },
  },
});
const lightTheme = createTheme({
  palette: {
    primary: {
      light: blue[200],
      main: blue[900],
      dark: "#28376f",
      contrastText: grey[500],
    },
    secondary: {
      main: blue[500],
    },
    background: {
      default: grey[900],
    },
  },
});

function App() {
  const [postsList, setPostsList] = useState([]);
  const [userId, setUserId] = useState("");

  async function handleRegistration(ev: any) {
    ev.preventDefault();
    try {
      console.dir(ev.target);

      let {
        firstName,
        lastName,
        username,
        email,
        password,
        confirmPassword,
        role,
        gender,
        birthDate,
      } = ev.target.elements;

      firstName = firstName.value;
      lastName = lastName.value;
      username = username.value;
      email = email.value;
      password = password.value;
      confirmPassword = confirmPassword.value;
      gender = gender.value;
      birthDate = birthDate.value;
      role = role.value;
      if (role !== process.env.role) {
        console.log(process.env.role);
        
        role = "public";
      }
      

      if (
        !firstName ||
        !lastName ||
        !username ||
        !email ||
        !password ||
        !confirmPassword ||
        !role ||
        !gender ||
        !birthDate
      )
        throw new Error(`something missing in handleRegister -client side`);
      if (password !== confirmPassword)
        throw new Error(`passwords dont match in register -client side`);
      const { data } = await axios.post("/users/add-newUser", {
        firstName,
        lastName,
        username,
        email,
        password,
        role,
        gender,
        birthDate,
      });
    } catch (error) {
      console.log(error);
    }
  }

  function handleLogin(ev: any) {
    ev.preventDefault();
    try {
    } catch (error) {
      console.log(error);
    }
  }

  async function getPostsList() {
    const { data } = await axios("https://jsonplaceholder.typicode.com/todos");
    let halfArray: any = [];
    for (let i = 0; i < 100; i++) {
      halfArray.push(data[i]);
    }
    setPostsList(halfArray);
  }

  useEffect(() => {
    getPostsList();
  }, []);
  const [theme, setTheme] = useState(true);
  if (theme) {
    var { primary, secondary, background } = darkTheme.palette;
  } else {
    var { primary, secondary, background } = lightTheme.palette;
  }
  return (
    <ThemeProvider theme={theme ? lightTheme : darkTheme}>
      <div
        style={{ background: background.default, color: primary.contrastText }}
        className="App"
      >
        <CssBaseline />
        <p>{theme ? "light" : "dark"}</p>

        <NavBar
          setTheme={setTheme}
          theme={theme}
          lightTheme={lightTheme}
          darkTheme={darkTheme}
          handleRegistration={handleRegistration}
          handleLogin={handleLogin}
        />
        <div className="wrapper">
          <NewPostForm setPostsList={setPostsList} />
          {postsList ? (
            <Feed
              setPostsList={setPostsList}
              postsList={postsList}
              theme={theme}
              lightTheme={lightTheme}
              darkTheme={darkTheme}
            ></Feed>
          ) : null}
        </div>
      </div>
    </ThemeProvider>
  );
}

export default App;
